import Resolver from "@/shared/api/resolver";
export class UserResolver {
    constructor() {
        this.apiResolver = new Resolver("users");
    }
    async create(user) {
        return await this.apiResolver.request("", "POST", user);
    }
    async updateById(user) {
        return await this.apiResolver.request(user.id.toString(), "PUT", user);
    }
    async deleteById(id) {
        return await this.apiResolver.request(id.toString(), "DELETE", null);
    }
    async getCurrentUser() {
        return await this.apiResolver.request("me", "GET", null);
    }
    async getAll() {
        return await this.apiResolver.request("", "GET", null);
    }
}
